.sg-custom-check {
    .sg-alternative-display {
        overflow: visible;
        
        .remTop(0.2);
        .transition(0s);
    }

    input:checked ~ .sg-alternative-display {
        background-color: transparent;
        background-image: url(../../dist/img/checkmark-upc-business.png);
        background-repeat: no-repeat;
        background-size: 100% 100%;
        border: none;

        .remHeight(1.9);
        .remMarginTop(-0.3);
        .remWidth(1.8);
    }

    .sg-icon-check:after {
        content: '';
    }

    &:hover {
        input:checked ~ .sg-alternative-display {
            background-color: transparent;
        }
    }
}
