.chzn-single,
.sg-handle {
    .vertical(@sgColorWhite, #f7f7f7);
}

.chzn-results {
    .box-shadow(0 2px 2px rgba(0,0,0,.15));
}

.chzn-container {
    .chzn-single {
        .remPaddingRight(5);

        .sg-handle .sg-divider:after {
            content: '\e62b';
            font-family: 'LGI-Iconfont';

            .remFontSize(1.3);
            .remRight(1.2);
        }
    }

    .chzn-drop {
        .chzn-results {
            .active-result {
                .remPaddingBottom(0.9);
                .remPaddingTop(0.9);
            }
        }
    }
}