.sg-btn {
    border-bottom: none !important;
    position: relative;

    .remMinHeight(@sgMiscHeightElements);
    .transition(0s);

     &.sg-btn-small {
        line-height: 0;

        .remFontSize(1.6);
        .remMaxHeight(2.7);
        .remMinHeight(2.7);
        .remPadding(@sgPaddingButtonSmallTop, @sgPaddingButtonSmallRight, @sgPaddingButtonSmallBottom, @sgPaddingButtonSmallLeft);

        &.sg-without-icon {
            .remPaddingRight(@sgPaddingButtonSmallLeft);
        }
        
        &+ .sg-btn-icon {
            &:before {
                .remFontSize(1.1);
                .remLeft(-1.6);
                .remTop(0.3);
            }
        }
    }

    &.sg-blue {
        .sg-btn-upc-business-blue()
    }

    &.sg-disabled {
        .sg-btn-upc-business-disabled()
    }

    &.sg-orange {
        .sg-btn-upc-business-orange()
    }

    &.sg-white {
        .sg-btn-upc-business-white()
    }

    &.sg-without-icon {
        .remPaddingRight(@sgPaddingButtonLeft);
    }
        
    &+ .sg-btn-icon {
        position: relative;

        &:before {
            position: absolute;

            .remFontSize(1.3);
            .remLeft(-2);
            .remTop(0.2);
        }
    }
}

button.sg-btn.sg-btn-small {
    .remPaddingTop(0.2);
}

.sg-btn-upc-business-blue() {
    text-shadow: 0 -1px @sgColorButtonBlueTextShadow;

    .box-shadow(0px 1px @sgColorButtonBlueBorderBottom);
    .sg-give-gradient-to-element(@sgColorButtonBlue, @sgColorButtonBlue, @sgColorButtonBlueHoverStart, @sgColorButtonBlueHoverEnd, @sgColorButtonBlueText, @sgColorButtonBlueText, @sgColorButtonBlueBorder, @sgColorButtonBlueBorder);
    .sg-give-active-color-to-element(@sgColorButtonBlueActive, @sgColorWhite, @sgColorButtonBlueActiveBorder);

    &+ .sg-btn-icon:before {
        color: @sgColorWhite;
        text-shadow: 0 -1px @sgColorButtonBlueTextShadow;
    }
}

.sg-btn-upc-business-orange() {
    text-shadow: 0 -1px @sgColorButtonOrangeTextShadow;

    .box-shadow(0px 1px @sgColorButtonOrangeBorderBottom);
    .sg-give-gradient-to-element(@sgColorButtonOrange, @sgColorButtonOrange, @sgColorButtonOrangeHoverStart, @sgColorButtonOrangeHoverEnd, @sgColorButtonOrangeText, @sgColorButtonOrangeText, @sgColorButtonOrangeBorder, @sgColorButtonOrangeBorder);
    .sg-give-active-color-to-element(@sgColorButtonOrangeActive, @sgColorWhite, @sgColorButtonOrangeActiveBorder);

    &+ .sg-btn-icon:before {
        color: @sgColorWhite;
        text-shadow: 0 -1px @sgColorButtonOrangeTextShadow;
    }
}

.sg-btn-upc-business-white() {
    .box-shadow(0px 1px @sgColorButtonWhiteBorderBottom);
    .sg-give-gradient-to-element(@sgColorButtonWhite, @sgColorButtonWhite, @sgColorButtonWhiteHoverStart, @sgColorButtonWhiteHoverEnd, @sgColorButtonWhiteText, @sgColorButtonWhiteText, @sgColorButtonWhiteBorder, @sgColorButtonWhiteBorder);
    .sg-give-active-color-to-element(@sgColorButtonWhiteActive, @sgColorWhite, @sgColorButtonWhiteActiveBorder);
    
    &+ .sg-btn-icon:before {
        color: @sgColorBlue;
    }
}

.sg-btn-upc-business-disabled() {
    .box-shadow(0px 1px #cecece);
    .sg-give-color-to-element(@sgColorButtonDisabled, @sgColorButtonDisabled, @sgColorButtonDisabledText, @sgColorButtonDisabledText, @sgColorButtonDisabledBorder, @sgColorButtonDisabledBorder);

    &+ .sg-btn-icon:before {
        color: #e3e3e3;
    }
}
